import 'common/css/radio.css'
import { AddSize, Remove } from 'api/size'
import { ERR_CODE } from 'api/config'
import { UserInfo } from 'common/js/common'

import { MessageBox } from 'mint-ui';
import { ShowToast } from 'common/js/loading-toast'

export default {
	props: {
		data: {
			type: Array,
			defalut: []
		},
		size: {
			type: Array,
			defalut: []
		},
		sizeData: {
			type: Object,
			defalut: []
		}
	},
	data() {
		return {
			userInfo: '',

			username: '',
			height: '',
			weight: '',
			chest: '',
			remarks: '',
			shoulder: '',
			waist: '',
			back: '',
			isCheckde: false,

			sizeList: [],
			indexArray: [],
			mArray: [],
			dataSize: [],

			picture: {}
		}
	},
	methods: {
		clickSize(info, index, listIndex, item) {
			if(item.title === this.picture[item.title]) {
				this.picture[item.title] = info.value;
			} else {
				this.picture[item.title] = info.value;
			}
			this.indexArray[listIndex].index = index;

		},
		setData() {
			this.$nextTick(_ => {
				console.log(this.size)
			})
		},
		cancle() {

			this.username = '';
			this.remarks = '';
			this.isCheckde = false;
			this.indexArray = [];
			this.sizeList = [];
			this.indexArray = [];
			this.picture = {};
			this.dataSize = [];
			this.mArray = [];
			this.$emit("close", false);

		},
		addSize() {

			if(this.userInfo === '') {
				this.userInfo = UserInfo();
			}
			if(this.username.trim() === '') {
				MessageBox('提示', '请输入用户姓名');
				return;
			}
			let obj = {};
			this.mArray.map((item, index) => {
				obj[this.size[index].title] = item;
			})
			let options = {
				title: this.username,
				remarks: this.remarks,
				body_digital_information: JSON.stringify(obj),
				body_picture_information: JSON.stringify(this.picture)
			}
			if(this.isCheckde) {
				options.type = 1;
			} else {
				options.type = 0;
			}
			if(this.sizeData.id) {
				options.id = this.sizeData.id;

				Remove(options).then(res => {
					if(res.data.code === ERR_CODE) {
						ShowToast('修改尺寸成功!');
						this.$emit("close", false);
						this.username = '';
						this.remarks = '';
						this.isCheckde = false;
						this.indexArray = [];
						this.sizeList = [];
						this.indexArray = [];
						this.picture = {};
			this.mArray = [];
						
						this.dataSize = [];

					}
				})
			} else {
				options.openId = this.userInfo.openId;
				options.status = 0;
				AddSize(options).then(res => {
					if(res.data.code === ERR_CODE) {
						ShowToast('添加尺寸成功!');
						this.$emit("close", false);
						this.username = '';
						this.remarks = '';
						this.isCheckde = false;
						this.indexArray = [];
						this.sizeList = [];
						this.indexArray = [];
						this.picture = {};
						this.dataSize = [];
						this.mArray = [];
					}
				})
			}

		}
	},
	created() {},
	mounted() {

	},
	watch: {
		data(val) {
			console.log(val);
			this.sizeList = val;
			for(let i = 0; i < val.length; i++) {
				let obj = {
					index: -1
				}
				this.indexArray.push(obj);
			}
			if(!this.sizeData.body)return;
			this.$nextTick(_ => {
				let data = JSON.parse(this.sizeData.body);
				this.sizeList.map((item, index) => {
					for(let i in data) {
						if(item.title === i) {
							item.sizeConfigValueList.map((info, oIndex) => {
								if(info.value === data[i]) {
									this.picture[item.title] = data[i];
									this.indexArray[index].index = oIndex;
								}

							})
						}
					}
				})
			})

		},
		size(val) {
			this.dataSize = val;
			if(!this.sizeData.body)return;
			this.$nextTick(_ => {
				let data = JSON.parse(this.sizeData.body);
				this.size.map((item, index) => {
					for(let i in data) {
						if(item.title === i) {
							this.mArray[index] = data[i]
						}
					}
				})

			})
		},
		sizeData(val) {
			this.sizeData = val;
			this.username = val.title;
			if(val.type === 1) {
				this.isCheckde = true;
			} else {
				this.isCheckde = false;
			}
			this.remarks = val.remarks;
		}
		//			title(val) {
		//				console.log(val);
		//			}
	}
}



